<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12  pt-3">
            <div class="col-md-12 p-0  mb-3" v-show="!isCheckExtraFeatureUSICBILLING">
                <dynamic-form lang="en" ref="validateRuleConfig" :buttons="buttons" v-show="!isCheckExtraFeatureUSICBILLING" :mandatorytext="$t('MandatoryString')" :schema="FormSchema"
                    v-on:OnSubmit="OnSubmit">
                </dynamic-form>
            </div>
            <div class="row" v-show="isCheckExtraFeatureUSICBILLING">
                <div class="col-md-8">
                    <dynamic-form lang="en" ref="validateRuleConfig" :buttons="false" v-show="isCheckExtraFeatureUSICBILLING" :mandatorytext="$t('MandatoryString')" :schema="FormSchemaForUSIC"
                        >
                    </dynamic-form>
                    
                </div>
                <div class="col-lg-4">
                    <div class="listing mt-4">
                        <div class="table-responsive" style="overflow: auto; max-height: 400px;">
                            <table class="table table-bordered">
                                <thead class="thead-bg">
                                    <tr>
                                        <th width="50%">{{ $t("fieldName") }}</th>
                                        <th>{{ $t("DataType") }}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(field, index) in groupFieldListOptions" :key="index">
                                        <td>{{ field.DISPLAY_NAME }}</td>
                                        <td>{{ field.DATA_TYPE }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
             
                <div class="col-12 float-left my-3">
                    <div class="row flex-row-reverse">
                        <div class="col-lg-6 text-right">
                            <a title="Save" @click="OnSubmit" class="btn btn-success mr-2"><i class="fa fa-floppy-o pr-2" aria-hidden="true"></i>{{ $t("SaveAndNextWizard") }}</a>
                            <a title="Close" @click="Close"  class="btn btn-primary delete-btn"><i class="fa fa-times pr-2" aria-hidden="true"></i>{{ $t("Close") }} </a>    
                        </div>
                        <div class="col-lg-6">
                            <small class="py-2 mandatory">{{ $t('Mandatory') }}</small>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
    
import DataService from "../../../services/DataService";
import {
    locateBillingService
} from "../../../services/locateBillingService";

export default {
    props: {
        configurationId: {
            type: String,
            required: false
        },
        serviceId: {
            type: String,
            required: false
        },
        reqType: {
            type: String,
            required: false
        }
    },
    data() {
        return {
            isLoading: false,
            ruleServiceList:[],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                       {
                            astype: "SelectField",
                            label: this.$t('Service'),
                            name: "Service",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('BillingRuleName'),
                            name: "BillingRuleName",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveFromDate'),
                            name: "EffectiveFromDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                //minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveToDate'),
                            name: "EffectiveToDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                // onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 5
                            },
                            validationRules: { "required": false, max: 2000 },
                        },
                    ]
                }
            ],
            FormSchemaForUSIC: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('BillingRuleName'),
                            name: "BillingRuleName",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CompanyName'),
                            name: "CompanyName",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SearchableSelectField",
                            label: this.$t('CONTRACTNAME'),
                            name: "ContractName",
                            value: "",
                            placeholder: "Select Client Name",
                            visibility: true,
                            isSearch: false,
                            config: {
                                options: [],
                                actionButtons: {
                                    selectAll: true,
                                    active: true,
                                    inactive: true,
                                    clear: true
                                },
                                isMultiple: false,
                                groupFields: ['groupText'],
                                minSearchLength: 0,
                                statusFieldConfig: {
                                    fieldName: 'status',
                                    activeValues: ['Active', 'active'],
                                    inactiveValues: ['Inactive', 'inactive'],
                                    activeColor: '#28a745',
                                    inactiveColor: '#dc3545'
                                },
                                onChange: this.onSelectionChange
                            },
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveFromDate'),
                            name: "EffectiveFromDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                //minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveToDate'),
                            name: "EffectiveToDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                // onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('GroupByForApplyingCharge'),
                            name: "GroupByForApplyingCharge",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ChargeTypeId'),
                            name: "ChargeTypeId",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('IsRuleApllicableToAll'),
                            name: "IsRuleApllicableToAll",
                            value: "",
                            disabled: false,
                            config: {
                                options: [{ value: "1", name: this.$t('Yes') }, { value: "0", name: this.$t('No') }],
                                onChange: this.onChangeApplicable
                            },
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('CheckCountInAllContract'),
                            name: "CheckCountInAllContract",
                            value: "",
                            disabled: false,
                            config: {options: [{ value: "1", name: this.$t('Yes') }, { value: "0", name: this.$t('No') }]},
                            validationRules: { "required": false, max: 100 },
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SearchableSelectField",
                            label: this.$t('UtilityName'),
                            name: "UtilityName",
                            value: "",
                            placeholder: "Select Utility Name",
                            visibility: true,
                            isSearch: false,
                            config: {
                                options: [],
                                actionButtons: {
                                selectAll: true,
                                active: true,
                                inactive: true,
                                clear: true},
                                groupFields: [],
                                isMultiple: true,
                                minSearchLength: 0,
                                statusFieldConfig: {
                                    fieldName: 'status',
                                    activeValues: ['Active', 'active'],
                                    inactiveValues: ['Inactive', 'inactive'],
                                    activeColor: '#28a745',
                                    inactiveColor: '#dc3545'
                                },
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ComboChargeScope'),
                            name: "ComboChargeScope",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveAndNextWizard'),
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close,
                }
            ],
            serviceList:[],
            mode :false,
            isCheckExtraFeatureUSICBILLING:false,
            groupFieldListOptions:[]
        }
    },
    created: async function () {
        let vm = this;
        await vm.CheckFeaturesForCompany('USICBILLING');
        if(this.$route.params.mode =='View'){
            vm.mode = true;
        }
        if (vm?.configurationId) {
                 vm.BindConfigurationData(vm.configurationId);
        }else{
            if(this.isCheckExtraFeatureUSICBILLING == true){
                vm.GetFieldsListBySetupId(0);
                vm.GetComboChargesListBySetupId(0);
            }
            const date = new Date(2099, 11, 31);
            const formattedDate = `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
            await vm.$nextTick();
            vm.$refs.validateRuleConfig.updateProperty("EffectiveToDate", "value", formattedDate)
            vm.$refs.validateRuleConfig.UpdateKeyValue();
        }
        if(this.isCheckExtraFeatureUSICBILLING == false){
            await vm.GetDDLBillingRuleService();
        }
        else
        {
            await vm.GetDDLCompanyList();
            await vm.GetBillingChargeTypeList();
            await vm.GetDDLCompanyContractClients();
        }
      
        //  If in view mode, disable all fields
        if (vm.mode) {
            vm.FormSchema.forEach(section => {
                section.Data.forEach(field => {
                    field.disabled = true;
                });
            });
            vm.buttons.forEach(btn => {
                if (btn.type === 'submit') {
                    btn.isVisible = false; 
                }
            });
        }
    },
    methods: {
        CheckFeaturesForCompany: async function (type) {
            var vm = this;
            var appendingUrl = `featureCode=${type}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if(type == 'USICBILLING'){
                    localStorage.setItem('isCheckExtraFeatureUSICBILLING', false);
                    if (response.data == true) {
                         vm.isCheckExtraFeatureUSICBILLING = true;
                         localStorage.setItem('isCheckExtraFeatureUSICBILLING',vm.isCheckExtraFeatureUSICBILLING);
                    } 
                }
            });
        },
        Close() {
            var vm = this;
            if(vm.reqType == 'fromServiceMapping'){
                this.$parent.$parent.$parent.isModalBillingConfiguration=false;
                this.$parent.$parent.$parent.FetchData();
            }
            else
            {
                vm.$router.push({ name: 'BillingRuleListing' });
            }
        },
        GetDDLCompanyList: async function () {
            var vm = this;
            await DataService.GetDdlCompanyList().then((response) => {
                if (response.data != '') {
                    if (response.data.length) {
                        const optionsArray = response.data.map(element => ({
                            name: element.Company_Name,
                            value: element.company_id
                        }));
                        vm.$refs.validateRuleConfig.updateProperty("CompanyName", "config.options", optionsArray);
                        vm.$refs.validateRuleConfig.UpdateKeyValue();
                    }
                }
            })
        },
        GetBillingChargeTypeList: async function () {
            var vm = this;
            await DataService.GetBillingChargeTypeList().then((response) => {
                if (response.data != '') {
                    if (response.data.length) {
                        const optionsArray = response.data.map(element => ({
                            name: element.CATEGORY_NAME,
                            value: element.CATEGORY_ID
                        }));
                        vm.$refs.validateRuleConfig.updateProperty("ChargeTypeId", "config.options", optionsArray);
                        vm.$refs.validateRuleConfig.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLCompanyContractClients: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            await locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.validateRuleConfig.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.validateRuleConfig.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = []; 
                vm.$refs.validateRuleConfig.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.validateRuleConfig.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        GetFieldsListBySetupId: async function (setupId) {
            var vm = this;
            var url = `setupId=${setupId}&isGroupBy=true`;
            await DataService.GetFieldsListBySetupId(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    const optionsArray = JSON.parse(response.data).map(element => ({
                            name: element.DISPLAY_NAME,
                            value: element.TEMPLATE_ID
                        }));
                    vm.$refs.validateRuleConfig.updateProperty("GroupByForApplyingCharge", "config.options", optionsArray);
                    vm.$refs.validateRuleConfig.UpdateKeyValue();
                }
            });
            await vm.BindFieldListBySetupId(setupId);
        },
        BindFieldListBySetupId: async function(setupId){
            var vm = this;
            var url = `setupId=${setupId}`;
            await DataService.GetFieldsListBySetupId(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.groupFieldListOptions = JSON.parse(response.data);
                }
            });
        },
        onChangeApplicable() {
            var utilityselectval = this.$refs.validateRuleConfig.getPropertyValue("IsRuleApllicableToAll", "value");
            switch (utilityselectval) {
                case true:
                    this.$refs.validateRuleConfig.updateProperty("UtilityName", "visibility", false);
                    this.$refs.validateRuleConfig.updateProperty("UtilityName", "validationRules", false);
                    // this.$refs.validateRuleConfig.updateProperty("UtilityName", "value", "");
                    break;
                default:
                    this.$refs.validateRuleConfig.updateProperty("UtilityName", "visibility", true);
                    this.$refs.validateRuleConfig.updateProperty("UtilityName", "validationRules", true);
            }

        },
        onSelectionChange: async function(value, x) {
            var vm = this;
            vm.isLoading = true;
            vm.selectedClientId = x;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            await locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                let SelectedUtilityList = [];
                var UtilityList = response.data;
                if (UtilityList != null) {
                    UtilityList.forEach(item => {
                        let obj = {
                            description: null,
                            displayValue: item.UTILITY_NAME,
                            groupText: null,
                            groupValue: null,
                            metaData: null,
                            text: item.UTILITY_NAME,
                            value: item.UTILITY_ID
                        };
                        SelectedUtilityList.push(obj);
                    });
                     vm.$refs.validateRuleConfig.updateProperty("UtilityName", "config.options", SelectedUtilityList);
                }
            }).catch(error => {
                vm.isLoading = false;
                  vm.$refs.validateRuleConfig.updateProperty("UtilityName", "config.options", []);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        GetComboChargesListBySetupId: function (setupId) {
            var vm = this;
            var url = `setupId=${setupId}&isComboChargeScope=true`;
            DataService.GetFieldsListBySetupId(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    const optionsArray = JSON.parse(response.data).map(element => ({
                            name: element.DISPLAY_NAME,
                            value: element.TEMPLATE_ID
                        }));
                    vm.$refs.validateRuleConfig.updateProperty("ComboChargeScope", "config.options", optionsArray);
                    vm.$refs.validateRuleConfig.UpdateKeyValue();
                }
            });
        },
        GetDDLBillingRuleService: async function () {
            var vm = this;
            debugger;
            var params = `id=&moduleName=&type=BILLING_RULE_SERVICE_NAME&search=`
            await DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.serviceList = response.data.DATA;
                        this.FormSchema.forEach(function(grpItem){
                                grpItem.Data.forEach(function(item){
                                    switch (item.name) {
                                        case "Service":
                                            item.config.options = vm.serviceList;
                                            break;
                                    }
                                });
                        });
                        var serviceId ='';
                        if(this.reqType == 'fromServiceMapping'){
                            serviceId = this.serviceId;
                        }
                        else{
                           serviceId = parseInt(vm.$route.params.serviceId);
                        }
                        if(serviceId != 0){
                            vm.$refs.validateRuleConfig.updateProperty("Service", "value", serviceId);
                            vm.$refs.validateRuleConfig.updateProperty("Service", "disabled", true);
                            vm.$refs.validateRuleConfig.UpdateKeyValue();
                        }
                    }
                }
            })
        },
        BindConfigurationData: async function (configurationId) {
            var vm = this;
            this.isLoading = true;
            const url = `configId=${configurationId}&isForUtilityBilling=${this.isCheckExtraFeatureUSICBILLING == false ? true : false}`;
            const response = await DataService.GetConfigurationData(url);
            if (response && response.data && response.data.length > 0) {
                var configurationValue = response.data[0];
                const isExtra = this.isCheckExtraFeatureUSICBILLING;
                if(isExtra){
                    await vm.GetFieldsListBySetupId(configurationValue.setup_id);
                    await vm.GetComboChargesListBySetupId(configurationValue.setup_id);
                    await vm.onSelectionChange(null,configurationValue.client_id);
                    this.FormSchemaForUSIC.forEach(function(grpItem){
                    grpItem.Data.forEach(function(item){
                        switch (item.name) {
                            case "BillingRuleName":
                                item.value = configurationValue.billing_config_name;
                                break;
                            case "EffectiveFromDate":
                                item.value = configurationValue.efficative_date_from ;
                                break;
                            case "EffectiveToDate":
                                item.value = configurationValue.efficative_date_to;
                                break;
                            case "CompanyName":
                                item.value = configurationValue.company_id;
                                break;
                            case "ContractName":
                                item.value = configurationValue.client_id;
                                break;
                            case "GroupByForApplyingCharge":
                                item.value = configurationValue.group_by;
                                break;
                            case "ChargeTypeId":
                                item.value = configurationValue.CHARGE_TYPE_ID;
                                break;
                            case "ComboChargeScope":
                                item.value = configurationValue.COMBO_CHARGE_SCOPE;
                                break;
                            case "UtilityName":
                                item.value = configurationValue.selectDivision;
                                break;
                            case "IsRuleApllicableToAll":
                                item.value = configurationValue.is_rule_applicale_to_all;
                                break;
                            case "CheckCountInAllContract":
                                item.value = configurationValue.check_count_in_all_contract;
                                break;
                            
                        }
                    });
                });   
                }
                else if(!isExtra){
                    this.FormSchema.forEach(function(grpItem){
                    grpItem.Data.forEach(function(item){
                        switch (item.name) {
                            case "BillingRuleName":
                                item.value = configurationValue.RULE_NAME;
                                break;
                            case "EffectiveFromDate":
                                item.value = configurationValue.EFFICATIVE_DATE_FROM;
                                break;
                            case "EffectiveToDate":
                                item.value = configurationValue.EFFICATIVE_DATE_TO;
                                break;
                            case "Description":
                                item.value = configurationValue.DESCRIPTION;
                                break;
                            case "Service":
                                item.config.options = vm.serviceList;
                                item.value = configurationValue.BILLING_SERVICE_ID;
                                item.disabled = true;
                                break;
                        }
                    });
                });   
                }
                this.isLoading = false;
            }
        },
        setTimerDate: function (x, y, z) {
            var vm = this;
            if (y.name == "EffectiveFromDate") {
                vm.$refs.validateRuleConfig.updateProperty("EffectiveToDate", "config.minDate", x);
                vm.$refs.validateRuleConfig.UpdateKeyValue();
            }
        },

        async OnSubmit(formfields, val) {
            var vm = this;
            var jsonObj = {
                configurationId: vm?.configurationId ? vm.configurationId :0,
                billingRuleName: formfields.BillingRuleName,
                effectiveFromDate: formfields.EffectiveFromDate,
                effectiveToDate: formfields.EffectiveToDate,
                description: formfields.Description,
                billingServiceId : formfields.Service
            };
            var params = JSON.stringify(jsonObj);
            DataService.SaveBillingRuleConfiguration(params).then((response) => {
                if (response.data.status == "Success" && response.data.code == "1") {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Configuration")]), "success", true, vm.$t("Alert"));
                    setTimeout(function () {
                        if(vm.reqType !== 'fromServiceMapping'){
                            vm.$router.push({ name: 'ManageConfiguration', params: { id: respoens.data.result,serviceId : response.data.linkedId } });
                        }
                        vm.$emit('config-saved', { configId: response.data.result, goTo: 'NodeConfiguration' });
                    }, 200);
                }
                else if (response.data.status == "Success" && response.data.code == "2") {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Configuration")]), "success", true, vm.$t("Alert"));
                }
                else if (response.data.status == "Success" && response.data.code == "3") {
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Configuration')]), "failure", true, vm.$t('Alert'));
                }
            });
        },
        close: function () {

        }
    }
}
</script>